import './index.scss'
import { Select, Input, Button, message } from 'antd';
const { Option } = Select;
import {connect} from 'react-redux'
import { getbj,getuser } from '../../actions';
import { axios} from '&'
var style = {
    first: {
        fontWeight: 'bold',
        marginBottom: 7
    },
    second: {
        marginBottom: 20
    }
}

import {Toast} from 'antd-mobile'

export function showToastNoMask(msg) {
    Toast.hide();
    Toast.info(msg, 2, null, false);
  }

@connect(
    (state)=>{
        return{
            bj:state.getIn(['data','bj']),
            user:state.getIn(['user','user'])
        }
    }
)
export default class Mine extends Component {
    constructor() {
        super()
        this.state = {
            one: '请选择班级',
        }
    }
    onChange = (value) => {
        this.setState({
            one: value
        });
    };
    clear = () => {
        this.setState({
            one: '请选择班级',
        })
        this.one.state.value = ''
        this.two.state.value = ''
        this.three.state.value = ''
    }

    submit = () => {
        if (this.one.state.value == '' || this.two.state.value == '' || this.three.state.value == '' || this.state.one == ""||this.state.one == "请选择班级") {
            message.info('请完善信息后提交')
        } else {
            sessionStorage.username=this.one.state.value
            var date = Date.now()
            axios.post('/react/changemsg', {
                username: this.one.state.value,
                email: this.two.state.value,
                mobile: this.three.state.value,
                mintor: this.state.one,
                date
            }).then(res => {
                showToastNoMask(res.data.msg)
                this.props.dispatch(getuser({url:'/react/getuser'}))
            })
        }
    }
    componentDidMount(){
        this.props.dispatch(getbj({url:'/react/getbj'}))
        this.props.dispatch(getuser({url:'/react/getuser'}))
    }
    render() {
        var {
            one,
        } = this.state
        var {
            user
        }=this.props
        return (
            <div>
                {
                    user.username?<h2 style={style.first}>修改个人信息</h2>:<h2 style={style.first}>填写个人信息</h2>
                }
                <div style={style.second}>
                    <div style={style.first}>姓名</div>
                    <Input placeholder="姓名" allowClear style={{ width: 400 }} ref={el => this.one = el} />
                </div>

                <div style={style.second}>
                    <div style={style.first}>班级：</div>
                    <Select style={{ width: 400 }} onChange={this.onChange} value={one}>
                        {
                            this.props.bj.map((item, i) => {
                                return (
                                    <Option value={item.code} key={i}>{item.value}</Option>
                                )
                            })
                        }
                    </Select>
                </div>
                <div style={style.second}>
                    <div style={style.first}>邮箱：</div>
                    <Input placeholder="邮箱" allowClear style={{ width: 400 }} ref={el => this.two = el} />
                </div>
                <div style={style.second}>
                    <div style={style.first}>手机号：</div>
                    <Input placeholder="手机号" allowClear style={{ width: 400 }} ref={el => this.three = el}  />
                </div>
                <Button type='primary' style={{ marginRight: 50 }} onClick={this.submit}>提交</Button>
                <Button type='danger' onClick={this.clear}>重置</Button>
            </div>
        )
    }
}